.hero {
    height: 100vh;
    background-image: url('../img/BackgroundHero3.png'); /* Du byter till egen bild */
    background-size: cover;
    background-position: center;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
}

.hero-overlay {
    background: linear-gradient(rgba(255, 255, 255, 0.15), rgba(255, 255, 255, 0.15));
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.hero-content {
    text-align: center;
    color: white;
    animation: fadeIn 1.5s ease-in-out forwards;
    opacity: 0;
}
.hero-title {
    font-size: 3rem;
    margin-bottom: 10px;
    font-family: 'Playfair Display', serif;
}

.highlight {
    color: #f26722;
    font-weight: bold;
}

.hero-title,
.hero-subtitle {
    text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.7);
}


.hero-subtitle {
    font-size: 1.4rem;
    margin-bottom: 20px;
    font-weight: 300;
}
.hero-buttons {
    display: flex;
    gap: 20px; /* avstånd mellan knapparna */
    justify-content: center;
    margin-top: 2rem;
    flex-wrap: wrap;
}

.hero-button {
    background-color: #f26722;
    text-decoration: none; /* ← detta tar bort understrykning */
    color: white;
    padding: 12px 24px;
    font-size: 1rem;
    border: none;
    border-radius: 25px;
    cursor: pointer;
    font-weight: 600;
    transition: background-color 0.3s, transform 0.2s;
    box-shadow: 0 4px 10px rgba(0,0,0,0.2);
}

.hero-button:hover {
    background-color: #e9560e;
    transform: scale(1.05);
}


@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}
